<template>
  <card>
    <template v-slot:title>
      Algunas nius de hoy <newspaper-icon/>
    </template>
    <template v-slot:body>
      <div :class="$style.content">
        <div
          v-for="(item, index) in news"
          :key="index"
          :style="randomStyle()"
          :class="$style.item">
          {{ item.title }}
          <a v-if="item.link" :href="item.link" target="_blank">ver</a>
        </div>
      </div>
    </template>
  </card>
</template>

<script>
import { mapState } from 'vuex';
import NewspaperIcon from 'vue-material-design-icons/Newspaper.vue';

import Card from '@/components/Card';

export default {
  name: 'News',
  components: {
    Card,
    NewspaperIcon,
  },
  computed: {
    ...mapState(['news',]),
  },
  methods: {
    randomStyle() {
      const highlight = Math.random();
      return {
        fontWeight: highlight > 0.7 ? 'bold': 'normal',
      };
    },
  },
};
</script>

